#preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #999;
  z-index: 999;
}

body {
    background-color: black;
    font-family: 'Saira', sans-serif;
    font-size: 12px;
    padding: 0;
    margin: 0;
    color: white;
    overflow: hidden;
}

.blabla {
    position: absolute;
    right: 16%;
    bottom: 16%;
    text-shadow: 2px 2px 3px rgba(0,0,0,0.16);
    text-align: right;
    line-height: 1;

    h1 {
        font-size: 4rem;
        letter-spacing: 0.73rem;
        margin: 0;
    }

    h2 {
        font-size: 2rem;
        letter-spacing: 0.58rem;
        margin: 0 5px 0 0;
    }

    p {
        font-size: 1.5rem;
        margin: 1.5rem 0.8rem 0 0;

        a {
            transition: all .3s ease-out;
            color: white;
            text-decoration: none;

            &:hover {
                text-shadow: 0px 1px 8px rgba(255, 255, 255, 0.48);
            }
        }
    }
}

.overlay:before {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url("../img/lcd.png");
  opacity: 0.16;
}

.overlay:after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(0,0,0,0) 0%, rgba(0,0,0,0.7) 80%, rgba(0,0,0,1) 100%);
  opacity: .32;
}

@media only screen and (max-width: 768px) {

    .blabla {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 8%;
        text-align: center;
        line-height: 1.2;

        h1 {
            font-size: 2rem;
            letter-spacing: 0.55rem;
            margin: 0;
        }

        h2 {
            font-size: 1.2rem;
            letter-spacing: 0.15rem;
            margin: 0.25rem 0 0;
        }

        p {
            font-size: 1.1rem;
            margin: 1.5rem 0 0;
        }
    }
}

